header{
    padding-inline: 10%;
    padding-top: 30px;
}

.menu {
    display: flex;
    width: 50%;
    justify-content: space-between;
}

.menu a{
    text-decoration: none;
    color: #EF6633;
}

header svg{
    display: none;
}

@media (max-width: 769px) {
    header svg{display: block}
    .menu {display: none}
}